import React from'react';
import './TabsRow.css';
import { useState } from 'react';
import Nav from 'react-bootstrap/Nav';

function TabsRow() {
  const tabs = [
    { path: '/tb1', name: 'Contract Management' },
    { path: '/tb2', name: 'Estimation management' },
    { path: '/tb3', name: 'Order management' },
  ];
  const [key, setKey] = useState({tab: tabs[0].path});
  return (
    <Nav className="nav-tabs" variant="tabs" defaultActiveKey={key.tab} onSelect={(selectedKey) => alert(`selected ${selectedKey}`)}>
      {tabs.map((tab) => (
        <Nav.Item>
          <Nav.Link eventKey={tab.path}>{tab.name}</Nav.Link>
        </Nav.Item>
      ))}
    </Nav>
  );
}
export default TabsRow;